


.home {
   height: 100%;
   
    &__container {
        height: 100%;
    }
    &__top {
        position: relative;
        padding: 0 0 0 10em;
        display: flex;
        align-items: flex-start;
        flex-direction: column;
        justify-content: center;
        width: 100%;
        height: 42em;

        // margin-bottom: 50em;
        // overflow: hidden;
        z-index: 3;

        &::after {
            content:"";
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: linear-gradient(to bottom, rgba(35, 35, 35, 0.327), rgba(0, 0, 0, 0.439));
            z-index: -1;
        }

        @media (max-width: 768px) {
            padding: 10em 0 0 5.5em;
            font-size: calc(0 * 0vw + 1.2em);
            height: 68em;
            background-color: black;
        }

        @media (max-width: 500px) {
            padding: 5em 0 0 3em;
            height: 85em;
        }

        @media (max-width: 425px) {
            padding: 10em 0 0 3em;
            height: 100em;
        }
    }

    &__title {
        margin-bottom: .4em;
        font-size: 5em;
        color: white;

        > span {
            display: inline-block;
            color: $color;
        }
        
        @media (max-width: 768px) {
            font-size: calc(0 * 0vw + 8em);
        }

        @media (max-width: 425px) {
            font-size: calc(0 * 0vw + 11em);
        }

        @media (max-width: 375px) {
            margin-bottom: .6em;
            font-size: calc(0 * 0vw + 12em);
        }
    }

    &__content {
        margin-bottom: 1.8em;
        max-width: 50%;
        color: #ffffff;

        @media (max-width: 768px) {
            margin-bottom: 3em;
            max-width: 90%;
            font-size: calc(0 * 0vw + 2em);
        }

        @media (max-width: 425px) {
            font-size: calc(0 * 0vw + 2.9em);
            max-width: 100%;
        }
    }

    &__link {
        background: $color;
        color: white;
        border-radius: .5em;
        padding: .7em 1em;
        text-decoration: none;
        transition: background .5s ease-in-out, border-color .3s linear;
        border: 1px solid transparent;

        &:hover {
            background: transparent;
            border-color : $color;
        }

        @media (max-width: 768px) {
            font-size: calc(0 * 0vw + 2.2em);
        }

        @media (max-width: 425px) {
            font-size: calc(0 * 0vw + 3.5em);
        }
    }


    &__vid {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
        z-index: -1;

        @media (max-width: 768px) {
            display: none;
        }
    }
}